@font-face {
    font-family: 'icomoon';
    src:    url('../fonts/icomoon.eot?xzu84y');
    src:    url('../fonts/icomoon.eot?xzu84y#iefix') format('embedded-opentype'),
        url('../fonts/icomoon.ttf?xzu84y') format('truetype'),
        url('../fonts/icomoon.woff?xzu84y') format('woff'),
        url('../fonts/icomoon.svg?xzu84y#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-arrow:before {
    content: "\e904";
}
.icon-user:before {
    content: "\e900";
}
.icon-folder:before {
    content: "\e901";
}
.icon-mark:before {
    content: "\e902";
}
.icon-rss:before {
    content: "\e903";
}

